import { Link } from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardContent from '@material-ui/core/CardContent'
import CardHeader from '@material-ui/core/CardHeader'
import CardMedia from '@material-ui/core/CardMedia'
import Gravatar from 'react-gravatar'
import moment from 'moment'
import React, { Component } from 'react'
import Typography from '@material-ui/core/Typography'

import { ViewerContext } from '../../Context/ViewerProvider'
import styles from './styles'

export class ItemCard extends Component {
  render () {
    const { item, classes } = this.props
    return (
      <ViewerContext.Consumer>
        {({ viewer }) => (
          <Card className={classes.card}>
            <Link to={`/profile/${item.itemowner.id || viewer.id}`}>
              <CardMedia
                className={classes.media}
                image={item.imageurl}
                title={item.title}
              />
              <CardHeader
                title={item.itemowner.fullname || viewer.fullname}
                subheader={moment(new Date(item.created)).fromNow()}
                avatar={
                  <Gravatar
                    email={item.itemowner.email || viewer.email}
                    className={classes.itemCardGravatarImage}
                  />
                }
              />
            </Link>
            <CardContent>
              <Typography gutterBottom variant='headline' component='h2'>
                {item.title}
              </Typography>
              <Typography color='textSecondary' gutterBottom>
                {item.tags && item.tags.map(t => t.title).join(', ')}
              </Typography>
              <Typography variant='subheading' gutterBottom>
                {item.description}
              </Typography>
            </CardContent>
            <CardActions>
              {!item.borrower &&
                item.itemowner.id !== viewer.id &&
                <Button
                  size='large'
                  color='secondary'
                  variant='outlined'
                  className={classes.borrow}
                >
                  Borrow
                </Button>}
            </CardActions>
          </Card>
        )}
      </ViewerContext.Consumer>
    )
  }
}

export default withStyles(styles)(ItemCard)



