export default function validate (values) {
  const errors = {}

  if (this.fileInput.current && !this.fileInput.current.value) {
    errors.imageurl = "You'll need an image to submit your item."
  }

  if (!this.state.selectedTags.length) {
    errors.tags = 'Please select tags for your item.'
  }

  if (!values.title || values.title.length > 140) {
    errors.title = 'Please name your item'
  }

  if (!values.description || values.description.length > 500) {
    errors.description = 'Please provide a description for  your item.'
  }

  return errors
}



